/* ========================================================================
   #PROSE
   ======================================================================== */

/**
 * 1. First heading has a neutral margin-top
 */
.c-prose {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-top: 1.5em;

        &:first-child {

            margin-top: var(--space-medium); /* [1] */
        }
    }
}
